import React from "react";
import { useParams } from "react-router-dom";
import Carousel from "../UI/Carousel";
const ShowCourse =() =>{
    const {courseId} = useParams();
    const CardBigArr= [{name:"Categories" ,id:courseId, title:"Add Categories",
    description:"Add or Edit All Course Categories"},{name:"Topics" ,id:courseId, title:"Add Topics",
    description:"Add or Edit All Course Topics"},{name:"Modules" ,id:courseId, title:"Add Modules",
    description:"Add or Edit All Course Modules"},{name:"MileStones" ,id:courseId, title:"Add MileStones",
    description:"Add or Edit All Course MileStones"},{name:"Sub-Courses" ,id:courseId, title:"Add Sub-Courses",
    description:"Add or Edit All Course Sub-Courses"}]
return(
    <>
             
    <div className="outer-container "> <div className="content-topk">
              
              </div>   <div className="content-topk">
              
              </div> 
              <div className="outer-all-card-white">
    <h1>  Course </h1>
   
    <div>  Course id
        {courseId}
        <Carousel  type="CardBig" itemValue={CardBigArr}/>
        </div>    </div>
        </div>    
    </> 
)
}

export default ShowCourse;